<span class="hljs-tag">&lt;<span class="hljs-name">u-multi-select</span> <span class="hljs-attr">filter</span> <span class="hljs-attr">:filter-method</span>=<span class="hljs-string">"filterMethod"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"options"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"huge"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-multi-select</span>&gt;</span>
