                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Arkansas'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Arkansas'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'California'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'California'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Colorado'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Colorado'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Connecticut'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Connecticut'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Delaware'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Delaware'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Florida'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Florida'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Georgia'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Georgia'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Hawaii'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Hawaii'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Idaho'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Idaho'</span> },
                { <span class="hljs-attr">text</span>: <span class="hljs-string">'Illinois'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Illinois'</span> },
            ],
        };
    },
    <span class="hljs-attr">watch</span>: {
        value(newValue) {
            <span class="hljs-built_in">console</span>.log(newValue);
        }
    },
    <span class="hljs-attr">methods</span>: {
        filterMethod(data,query) {
            <span class="hljs-keyword">const</span> tdata = data.filter(<span class="hljs-function">(<span class="hljs-params">item</span>) =&gt;</span> {
                <span class="hljs-keyword">if</span>(item.value.toLowerCase().indexOf(query.toLowerCase()) &gt; <span class="hljs-number">-1</span>)
                    <span class="hljs-keyword">return</span> item;
                <span class="hljs-keyword">else</span>
                    <span class="hljs-keyword">return</span> <span class="hljs-literal">false</span>;
            });
            <span class="hljs-keyword">return</span> tdata;
        }
    }
};
