<h2>MultiSelect API</h2>
<h3>Attrs/Props</h3>
<table>
<thead>
<tr>
<th>Attr/Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>Array&lt;{ text, value }&gt;</td>
<td></td>
<td>Data书写方式中的数据列表</td>
</tr>
<tr>
<td>value.sync, v-model</td>
<td>Array</td>
<td></td>
<td>当前选择的值</td>
</tr>
<tr>
<td>field</td>
<td>String</td>
<td><code>'text'</code></td>
<td>显示文本字段</td>
</tr>
<tr>
<td>readonly</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否只读</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
<tr>
<td>size</td>
<td>String</td>
<td><code>normal normal</code></td>
<td>控制宽高属性，第一个值表示高度属性，第二值表示宽度，值有<code>small</code>,<code>normal</code>,<code>large</code>,<code>huge</code> 四个值可以互相组合</td>
</tr>
<tr>
<td>filter</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>支持输入，搜索匹配项</td>
</tr>
<tr>
<td>filter-method</td>
<td>Function</td>
<td></td>
<td>自定义输入搜索的方法</td>
</tr>
<tr>
<td>placeholder</td>
<td>String</td>
<td><code>'请选择'</code></td>
<td>默认展示的文字，在<code>filter=true</code>时生效</td>
</tr>
<tr>
<td>pattern</td>
<td>String</td>
<td><code>''normal</code></td>
<td>默认值是normal，如果要实现自主添加需要将值设置为<code>'create'</code>，标签折叠模式值为<code>'collapseTag'</code></td>
</tr>
<tr>
<td>collapseTagCount</td>
<td>Number</td>
<td><code>6</code></td>
<td>指定最多显示的 tag 数量，超出后折叠</td>
</tr>
<tr>
<td>collapseTagPlaceholder</td>
<td>String</td>
<td><code>'更多'</code></td>
<td>定义 tag 超出后折叠的显示内容</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@select</h4>
<p>选择某一项前触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.selected</td>
<td>Object</td>
<td>选择项对象</td>
</tr>
<tr>
<td>$event.value</td>
<td>Any</td>
<td>选择项的值</td>
</tr>
</tbody>
</table>
<h4>@close</h4>
<p>点击选中项的关闭图标</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.index</td>
<td>Number</td>
<td>点击选择项的索引</td>
</tr>
<tr>
<td>$event.value</td>
<td>Array</td>
<td>选择项的值</td>
</tr>
</tbody>
</table>
<h4>@toggle</h4>
<p>选择项展示和隐藏触发函数</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.open</td>
<td>Boolean</td>
<td>值表示展示还是隐藏</td>
</tr>
</tbody>
</table>
</u-article></template>

